* {
  font-family: Arial, Helvetica, sans-serif;
}
.App {
  text-align: center;
  background-color: #77d970;
  min-height: 100vh;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
}
.small-bg {
  width: 140px;
  height: 140px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
h1 {
  color: #fff;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.input {
  border: none;
  font-size: 16px;
  padding: 10px;
  margin-right: 4px;
  outline: none;
}
