@mixin blockCenter($align, $justify: $align) {
  display: flex;
  align-items: $align;
  justify-content: $justify;
}
.icon {
  width: 50px;
  height: 50px;
}
.header {
  margin: 0;
  color: #fff;
  text-transform: uppercase;
  padding-top: 100px;
  padding-bottom: 40px;
}
p {
  color: #fff;
}
.flex {
  @include blockCenter(center);
}
.list {
  list-style: none;
  padding: 0;
  max-height: 360px;
  overflow-x: hidden;
  // overflow: scroll;
  &-item {
    @include blockCenter(center, space-between);
    background-color: #ccc;
    text-align: center;
    padding: 14px;
    margin-bottom: 10px;
    min-width: 100px;
  }
}
